.CharSheet {
  display: grid;
  position: absolute;
  z-index: 300;
  top: 8vh;
  left: 67vw;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(10, 1fr);
  width: 80vw;
  height: 92vh;
  max-height: 92vh;
  background-image: url(../images/Paper.jpg);
  background-size: cover;
  padding-left: 2vw;
  color: black;
  -webkit-box-shadow: -5px 5px 6px 0px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: -5px 5px 6px 0px rgba(0, 0, 0, 0.6);
  box-shadow: -5px 5px 6px 0px rgba(0, 0, 0, 0.6);
  transition: ease-in-out 0.5s;

  h4 {
    font-size: calc(8px + 2vmin);
    padding: 4px 0;
  }

  .CharHeader {
    display: flex;
    flex-direction: column;
    grid-column: 1 / 6;
    grid-row: 1 / 2;

    .CharTitle {
      text-align: center;
    }

    .Name {
      display: block;
      height: 4vh;
      width: 100%;
      margin-top: 12px;
      font-size: calc(9px + 2vmin);
      line-height: 0.6;
    }
  }

  .CharInfo {
    display: flex;
    flex-direction: column;
    grid-column: 1 / 4;
    grid-row: 2 / 4;
    font-size: calc(8px + 2vmin);
    margin: 5px;
    border-top: 1px solid black;

    div {
      display: flex;
      flex-direction: row;

      p {
        width: 40%;
        font-size: calc(7px + 2vmin);
        font-family: inherit;
      }

      span {
        width: 50%;
        border-bottom: 1px solid black;
      }
    }
  }

  .CharAttributes {
    grid-column: 1 / 4;
    grid-row: 4 / 7;
    margin: 5px;
    font-size: calc(5px + 2vmin);
    border-top: 1px solid black;
  }

  .Portrait {
    grid-column: 3 / 5;
    grid-row: 2 / 6;
    width: 80%;
    height: 90%;
    margin-top: 5px;
    margin-left: 50%;
    border: 1px solid black;
  }

  .CharSkills {
    font-size: calc(3px + 2vmin);
    grid-column: 1 / 6;
    grid-row: 7 / 10;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 2px;
    border-top: 1px solid black;

    label {
      display: inline-block;
      width: 40%;
      height: 15%;
    }

    input {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border-radius: 50%;
      width: 14px;
      height: 14px;
      background-color: white;
      border: 2px solid rgb(0, 0, 0);
      transition: 0.2s all linear;
      margin-right: 5px;
    }

    input:checked {
      border: 7px solid black;
    }
  }

  .CharTraits {
    grid-column: 6 / -1;
    grid-row: 1 / 4;

    ul {
      height: 75%;
      width: 90%;
      margin-top: 1%;
      border: 1px solid black;

      li {
        list-style-position: inside;
        list-style-type: square;
        padding: 1%;
        margin-left: 1%;
      }
    }
  }

  .CharStory {
    grid-column: 6 / -1;
    grid-row: 4 / -1;

    p {
      font-size: calc(5px + 2vmin);
      height: 90%;
      width: 90%;
      margin: 3px;
      padding: 1%;
      border: 1px solid black;
      text-align: justify;
      white-space: pre-line;
      overflow-wrap: break-word;
    }
  }

  .Choice {
    font-weight: bold;
    margin-left: 1.5vh;
  }

  .Draw {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    background: #333;
    cursor: pointer;

    i {
      color: rgb(204, 197, 159);
    }
  }

  .Draw:hover {
    background: rgb(43, 43, 43);
  }

  .DrawOut {
    left: -40vw;
  }
}

.CharSheetDrawOut extends .CharSheet,
.DrawOut {
  display: grid;
  position: absolute;
  top: 8vh;
  left: 20vw;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(10, 1fr);
  width: 80vw;
  height: 92vh;
  max-height: 92vh;
  background-color: rgb(160, 135, 83);
  transition: ease-in-out 0.5s;
}